function calculate() {
    var number1InputElement = document.getElementById("number1");
    var number2InputElement = document.getElementById("number2");
    var number1string = number1InputElement.value;
    var number2string = number2InputElement.value;
    var number1number = parseInt(number1string);
    var number2number = parseInt(number2string);
    var operationElement = document.getElementById("operatorSelect");
    var operation = operationElement.value;
    var result = calc(operation, number1number, number2number);
    console.log("Result: " + result);
    var outputElement = document.getElementById("resultSpan");
    outputElement.innerHTML = result.toString();
    /*
    var result = number1number + number2number;
    console.log(result);

    let outputElement: HTMLOutputElement = <HTMLOutputElement>document.getElementById("resultSpan");
    outputElement.innerHTML = result.toString();
    */
}
function calc(operation, a, b) {
    switch (operation) {
        case "+":
            return a + b;
        case "-":
            return a - b;
        case "*":
            return a * b;
        case "/":
            return a / b;
        default:
            return -1;
    }
}
var sumButton = document.getElementById("sumButton");
sumButton.addEventListener("click", calculate);
